@tailwind base;
@tailwind components;
@tailwind utilities;

* {
	margin: 0;
	padding: 0;
}

@font-face {
	src: url("./fonts/Helvetica/Helvetica-Bold.ttf") format("truetype");
	font-family: hBold;
	font-display: swap;
}

@font-face {
	src: url("./fonts/Nunito/NunitoSans-Bold.ttf") format("truetype");
	font-family: nBold;
	font-display: swap;
}

@font-face {
	src: url("./fonts/Nunito/NunitoSans-Light.ttf") format("truetype");
	font-family: nLight;
	font-display: swap;
}

.max-width {
	max-width: 1366px;
	margin: 0 auto;
}

.margin {
	width: 95%;
	margin: 0 auto;
}

h1, h2 {
	color: #F29F3E;
	font-family: hBold;
}

.main-bg {
	background: url(./img/bg.jpg) no-repeat 100% 0%;
	background-size: cover;
	height: auto;
	padding-bottom: 50px;
	position: relative;
}

.main-bg .header {
	padding-top: 30px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
		 -ms-flex-pack: center;
			  justify-content: center;
}

.main-bg .header .header-intro p {
	font-family: nLight;
}

.main-bg .header .header-intro p:first-child {
	color: #A7A7A7;
}

.main-bg .header .header-intro p:nth-child(3) {
	color: #1a1a1a;
	width: 80%;
}

.main-bg .header .header-intro h1 {
	margin: 5px 0;
}

.main-bg .header .header-intro .socials {
	display: flex;
	align-items: center;
}

.main-bg .header .header-intro .socials a {
	margin-top: 10px;
	color: #F29F3E;
	display: inline-block;
}

.main-bg .header .header-intro a:not(:last-child) {
	margin-right: 5px;
}

.main-bg .header .header-image img {
	width: 200px;
	height: 250px;
}

.main-bg .projects {
	margin: 30px 0 50px 0;
}

.main-bg .projects h2 {
	margin: 30px 0;
}

.main-bg .projects .p-row {
	display: flex;
	-webkit-box-pack: justify;
	-ms-flex-pack: justify;
	justify-content: space-between;
}

.main-bg .projects .p-row .project {
	position: relative;
	width: 500px;
	height: 300px;
	cursor: pointer;
	overflow: hidden;
}

.main-bg .projects .p-row .project .project-image {
	width: 100%;
	height: 100%;
	transition: all .3s ease-in-out
}

.main-bg .projects .p-row .project .overlay {
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 100%;
	z-index: 10;
	display: flex;
	align-items: center;
	justify-content: center;
	background: rgba(0, 0, 0, 0.5);
	opacity: 0;
	transition: all .3s ease-in-out
}

.main-bg .projects .p-row .project .overlay p {
	color: white;
	font-family: nBold;
}

.main-bg .projects .p-row .project:hover .project-image {
	transform: scale(2) rotate(5deg);
}

.main-bg .projects .p-row .project:hover .overlay {
	opacity: 1;
}

.main-bg .stacks {
	margin-bottom: 50px;
}

.main-bg .stacks h2 {
	margin: 0;
}

.main-bg .stacks .s-sub-title {
	color: #1a1a1a;
	font-size: 14px;
	font-family: nLight;
	margin-bottom: 30px;
}

.main-bg .stacks .s-row {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
}

.main-bg .stacks .s-row:not(:last-child) {
	margin-bottom: 15px;
}

.main-bg .stacks .s-row .stack {
	border: 1px solid #F29F3E;
	border-radius: 50px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	align-items: center;
	padding: 10px 0;
	width: 170px;
}

.main-bg .stacks .s-row .stack:not(:last-child) {
	margin-right: 15px;
}

.main-bg .stacks .s-row .stack p {
	font-family: nLight;
	color: #1a1a1a;
	margin-left: 5px;
}

.main-bg .education h2 {
	margin: 30px 0;
}

.main-bg .education .schools {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
}
 
 .main-bg .education .schools .school {
	text-align: center;
 }
 
 .main-bg .education .schools .school img {
	border: 1px solid #F29F3E;
	border-radius: 100%;
	padding: 5px;
 }
 
 .main-bg .education .schools .school .school-name {
	font-family: nBold;
	color: #1a1a1a;
	margin-top: 5px;
	width: 80%;
	margin: 0 auto;
 }
 
 .main-bg .education .schools .school .school-course {
	font-family: nLight;
	font-size: 14px;
	margin: 5px 0;
 }
 
 .main-bg .education .schools .school .date {
	font-family: nLight;
	color: #858585;
	font-size: 12px;
 }
 

 
 @media only screen and (max-width: 950px) {
	.main-bg {
	  background: #F3F3F3;
	}
	.main-bg .header {
	  -webkit-box-orient: vertical;
	  -webkit-box-direction: reverse;
			-ms-flex-direction: column-reverse;
				 flex-direction: column-reverse;
	  -webkit-box-align: center;
			-ms-flex-align: center;
				 align-items: center;
	}
	.main-bg .header .header-intro {
	  text-align: center;
	}
	.main-bg .header .header-intro p:nth-child(3) {
	  margin: 0 auto;
	  text-align: center;
	}
	.main-bg .header .header-image img {
	  width: 220px;
	  height: 180px;
	  margin-bottom: 15px;
	}
 }
 
 @media only screen and (max-width: 850px) {
	.main-bg .projects {
	  margin: 30px 0 50px 0;
	}
	.main-bg .projects h2 {
	  margin: 30px 0;
	}
	.main-bg .projects .p-row .project {
	  width: 49%;
	  height: 450px;
	  display: -webkit-box;
	  display: -ms-flexbox;
	  display: flex;
	  -webkit-box-pack: center;
			-ms-flex-pack: center;
				 justify-content: center;
	  -webkit-box-orient: vertical;
	  -webkit-box-direction: normal;
			-ms-flex-direction: column;
				 flex-direction: column;
	}
	

	.main-bg .stacks {
	  text-align: center;
	  margin-bottom: 50px;
	}
	.main-bg .stacks h2 {
	  margin: 15px 0;
	}
	.main-bg .stacks .s-row {
	  -webkit-box-orient: vertical;
	  -webkit-box-direction: normal;
			-ms-flex-direction: column;
				 flex-direction: column;
	  -webkit-box-align: center;
			-ms-flex-align: center;
				 align-items: center;
	}
	.main-bg .stacks .s-row:not(:last-child) {
	  margin-bottom: 0;
	}
	.main-bg .stacks .s-row .stack {
	  margin-bottom: 15px;
	}
	.main-bg .stacks .s-row .stack:not(:last-child) {
	  margin-right: 0;
	}
	.main-bg .education {
	  text-align: center;
	}
	.main-bg .education h2 {
	  margin: 30px 0;
	}
	.main-bg .education .schools {
	  display: -webkit-box;
	  display: -ms-flexbox;
	  display: flex;
	  -webkit-box-pack: center;
			-ms-flex-pack: center;
				 justify-content: center;
	  -webkit-box-orient: vertical;
	  -webkit-box-direction: normal;
			-ms-flex-direction: column;
				 flex-direction: column;
	  -webkit-box-align: center;
			-ms-flex-align: center;
				 align-items: center;
	}
	.main-bg .education .schools .school {
	  text-align: center;
	}
	.main-bg .education .schools .school:not(:last-child) {
	  margin-bottom: 30px;
	}
 }
 
 @media only screen and (max-width: 650px) {
	.main-bg .projects {
	  text-align: center;
	}
	.main-bg .projects .p-row {
	  -webkit-box-pack: center;
			-ms-flex-pack: center;
				 justify-content: center;
	  -webkit-box-orient: vertical;
	  -webkit-box-direction: normal;
			-ms-flex-direction: column;
				 flex-direction: column;
	  -webkit-box-align: center;
			-ms-flex-align: center;
				 align-items: center;
	}
	.main-bg .projects .p-row .project {
	  height: auto;
	  margin-bottom: 30px;
	  width: 100%;
	}
}

@media only screen and (max-width: 480px) {
	.main-bg .header .header-image img {
	  width: 200px;
	  height: 150px;
	}
}

::-webkit-scrollbar {
	width: 7px;
}

::-webkit-scrollbar-track {
	background: #fff;
}

::-webkit-scrollbar-thumb {
	background: #F29F3E;
}